<template>
  <div class="shopcart-container">
    <!-- 购物车 -->
    <div class="shopcart">
        <div class="content" @click="toggleList()">
          <div class="content-left">
            <div class="logo-wrapper">
              <div class="logo" :class="{'highlight':totalCount>0}">
                <i class="icon-shopping_cart" :class="{'highlight':totalCount>0}"></i>
              </div>
              <div class="num" v-show="totalCount>0">
                {{totalCount}}
              </div>
            </div>
            <div class="price" :class="{'highlight':totalPrice>0}">&yen;{{totalPrice}}</div>
            <div class="desc">另需配送费&yen;{{deliveryPrice}}</div>
          </div>
          <div class="content-right">
            <div class="pay" :class="payClass"  @click.stop.prevent="pay()">
              {{payDesc}}
            </div>
          </div>
        </div>
        <div class="ball-container">
          <div v-for="ball in balls">
            <transition name="drop" 
              @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
              <div v-show="ball.show" class="ball">
                <div class="inner inner-hook">
                </div>
              </div>
            </transition>
          </div>
        </div>
        <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty()">清空</span>
          </div>
          <div class="list-content" ref="listContent">
            <ul>
              <li v-for="food in selectFoods" class="food">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>&yen;{{food.count*food.price}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                <cart-control :food="food"></cart-control>
                </div>
              </li>
            </ul>
          </div>
        </div>
        </transition>
    </div>
    <!-- 遮罩层 -->
    <!-- 遮罩层要和shopcart同级 最外层要加一层嵌套 -->
    <transition name="fade">
      <div class="list-mask" v-show="listShow" @click="hideList()"></div>
    </transition>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import cartControl from 'components/cartcontrol/cartcontrol'
export default {
  name: 'shopcart',
  data () {
    return {
      balls: [{show: false}, {show: false}, {show: false}, {show: false}, {show: false}],
      dropBalls:[],
      fold:true
    }
  },
  components: {
    cartControl
  },
  props:{
    selectFoods:{
      type:Array,
      default(){
        return []
      }
    },
    deliveryPrice :{
      type:Number,
      default:0
    },
    minPrice: {
      type:Number,
      default:0
    }
  },
  computed:{
    totalPrice(){
      let total = 0;
      this.selectFoods.forEach((food) => {
        total += food.price*food.count
      })
      return total
    },
    totalCount(){
      let count=0;
      this.selectFoods.forEach((food) => {
        count += food.count
      })
      return count
    },
    payDesc(){
      if(this.totalPrice===0){
        return `¥${this.minPrice}起送`
      }else if(this.totalPrice<this.minPrice){
        let diff = this.minPrice-this.totalPrice;
        return `还差 ¥${diff}元起送`
      }else{
        return '去结算'
      }
    },
    payClass(){
      if(this.totalPrice<this.minPrice){
        return 'not-enough'
      }else{
        return 'enough'
      }
    },
    listShow(){
      //totalCount==0的情况
      if(!this.totalCount){
        this.fold=true;
        return false
      }
      let show = !this.fold;
      //列表显示才用到scroll
      if(show) {
          this.$nextTick(() => {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$refs.listContent, {
                click: true
              });
            } else {
              this.scroll.refresh();
            }
          });
        }
      return show
    }
  },
  methods:{
    drop(el){
      //console.log(el);//可以打印当前点击的加号按钮
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if(!ball.show){
          ball.show=true;
          ball.el=el;
          this.dropBalls.push(ball);
          return 
        }
        ball.show=true;
      };
    },
    beforeEnter: function (el) {
      let count = this.balls.length;
      while (count--) {
        let ball = this.balls[count];
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect();
          let x = rect.left - 32;
          let y = -(window.innerHeight - rect.top - 22);
          el.style.display = '';
          el.style.webkitTransform = `translate3d(0,${y}px,0)`;
          el.style.transform = `translate3d(0,${y}px,0)`;
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
          inner.style.transform = `translate3d(${x}px,0,0)`;
        }
      }
    },
    enter: function (el) {
        //浏览器重绘
        let rf = el.offestHeight;
        this.$nextTick(() => {
          el.style.webkitTransform = 'translate3d(0,0,0)';
          el.style.transform = 'translate3d(0,0,0)';
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = 'translate3d(0,0,0)';
          inner.style.transform = 'translate3d(0,0,0)';
        });
      },
    afterEnter: function (el) {
      let ball = this.dropBalls.shift();
      if (ball) {
        ball.show = false;
        el.style.display = 'none';
      }
    },
    toggleList(){
      if(!this.totalCount){
        return 
      }
      this.fold = !this.fold;
    },
    empty(){
      this.selectFoods.forEach((food) => {
        food.count=0;
      })
    },
    hideList(){
      this.fold=true;
    },
    pay(){
      if(this.totalPrice<this.minPrice){
        return 
      }
      window.alert(`需支付${this.totalPrice}元`)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../../assets/scss/mixin';
  .shopcart{
    position: fixed;
    bottom:0;
    left:0;
    z-index:50;
    width:100%;
    height:48px;
    .content{
      display:flex;
      font-size:0;
      background:#141d27;
      color:rgba(255,255,255,.4);
      .content-left{
        flex:1;
        .logo-wrapper{
          display:inline-block;
          position: relative;
          top:-10px;
          margin:0 12px;
          padding:6px;
          width:56px;
          height:56px;
          box-sizing:border-box;
          vertical-align:top;
          border-radius:50%;
          background:#141d27;
          text-align:center;
          .logo{
            width:100%;
            height:100%;
            background:#2b343c;
            border-radius:50%;
            &.highlight{
              background: rgb(0,160,220);
            }
            .icon-shopping_cart{
              font-size:24px;
              color:#80858a;
              line-height:44px;
              &.highlight{
                color: #fff;
              }
            }
          }
          .num{
            position: absolute;
            top: 0;
            right: 0;
            width: 24px;
            height: 16px;
            line-height: 16px;
            text-align: center;
            border-radius:16px;
            font-size:9px;
            color: #fff;
            font-weight: 700;
            background: rgb(240,20,20);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,.4);
          }
        }
        .price{
          display:inline-block;
          vertical-align:top;
          line-height:24px;
          margin-top:12px;
          box-sizing:border-box;
          padding-right:12px;
          border-right:1px solid rgba(255,255,255,.1);
          font-size:16px;
          font-weight:700;
          &.highlight{
            color: #fff;
          }
        }
        .desc{
          display:inline-block;
          vertical-align:top;
          line-height:24px;
          margin:12px 12px 0 12px;
          font-size:10px;
        }
      }
      .content-right{
        flex:0 0 105px;
        .pay{
          width:105px;
          background:#2b333b;
          text-align:center;
          height:48px;
          line-height:48px;
          font-weight:700;
          font-size:12px;
          &.not-enough{
            background: #2b333b;
          }
          &.enough{
            background: #00b43c;
            color: #fff;
          }
        }
      }
    }
    .ball-container{
      .ball{
        position: fixed;
        left:32px;
        bottom: 22px;
        z-index: 200;
        transition: all 0.6s cubic-bezier(0.49, -0.29, 0.75, 0.41);
        /*&.drop-enter-active, &.drop-leave-active {
          transition: all .4s;
        }*/
        .inner{
          width:16px;
          height:16px;
          border-radius:50%;
          background:rgb(0, 160, 220);
          transition: all 0.4s linear;
        }
      }
    }
    .shopcart-list{
      position: absolute;
      left: 0;
      top:0;
      z-index: -1;
      width:100%;
      transform:translate3d(0, -100%, 0);
      transition: all .4s;
      &.fold-enter-active, &.fold-leave-active {
        transform: translate3d(0, -100%, 0);
      }
      &.fold-enter,&.fold-leave-active {
        transform: translate3d(0, 0, 0);
      }
      .list-header{
        height:41px;
        line-height:41px;
        padding:0 18px;
        background:#f3f5f7;
        border-bottom:1px solid rgba(7, 17, 27, 0.1);
        .title{
          float:left;
          font-size:14px;
          color:rgb(7, 17, 27);
        }
        .empty{
          float: right;
          font-size:14px;
          color:rgb(0, 160, 220);
        }
      }
      .list-content{
        padding:0 18px;
        max-height:217px;
        overflow:hidden;
        background:#fff;
        .food{
          position:relative;
          padding:12px 0;
          box-sizing:border-box;
          @include border-1px();
          .name{
            line-height:24px;
            font-size:14px;
            color:rgb(7, 17, 27);
          }
          .price{
            position:absolute;
            right:90px;
            bottom:12px;
            line-height:24px;
            font-size:14px;
            font-weight:700;
            color:rgb(240, 20, 20);
          }
          .cartcontrol-wrapper{
            position:absolute;
            right:0;
            bottom:6px;
          }
        }
      } 
    }
  }
.list-mask{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:40;

  opacity:1;
  background:rgba(7, 17, 27, 0.6);

  &.fade-enter-active, &.fade-leave-active {
    opacity:1;
    transition: all 0.5s;
    background:rgba(7, 17, 27, 0.6);
  }
  &.fade-enter, &.fade-leave-active {
    opacity:0;
    background:rgba(7, 17, 27, 0);
  }
}
</style>



